import { Component, OnInit } from '@angular/core';
import { trigger, style, transition, animate, 
  keyframes, query, stagger } from '@angular/animations';
 //import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-couple',
  templateUrl: './couple.component.html',
  styleUrls: ['./couple.component.css'],
  animations: [
	  trigger('goals', [
		  transition('* => *', [
			  query(':enter', style({opacity: 0}),{optional: true}),
			  query(':enter', stagger('2500ms',[
				  animate('20s ease-in', keyframes([
					  style({opacity: 0, transform: 'translateX(50%)', offset: 0}),
					  style({opacity: .5, transform: 'translateY(35px)', offset: 0.166}),
					  style({opacity: 0.5, transform: 'translateX(75%)', offset: 0.33}),
					  style({opacity: .5, transform: 'translateY(335px)', offset: 0.5}),
					   style({opacity: .5, transform: 'translateX(5%)', offset: 0.666}),
					  style({opacity: .5, transform: 'translateY(35px)', offset: 0.833}),
				  	style({opacity: 1, transform: 'translateY(0)', offset: 1})
					]))
				]),{optional: true})
			])
		]),
	  trigger('dash', [
		  transition('* => *', [
			  query(':enter', style({opacity: 0}),{optional: true}),
			  query(':enter', stagger('2500ms',[
				  animate('20s ease-in', keyframes([
					  style({opacity: 0, transform: 'translateX(50%)', offset: 0}),
					  style({opacity: .5, transform: 'translateY(35px)', offset: 0.166}),
					  style({opacity: 0.5, transform: 'translateX(75%)', offset: 0.33}),
					  style({opacity: .5, transform: 'translateY(335px)', offset: 0.5}),
					   style({opacity: .5, transform: 'translateX(5%)', offset: 0.666}),
					  style({opacity: .5, transform: 'translateY(35px)', offset: 0.833}),
				  	style({opacity: 1, transform: 'translateY(0)', offset: 1})
					]))
				]),{optional: true})
			])
		])

 	]


})
export class CoupleComponent implements OnInit {

  goals = ["Spartagus", "Weds" , "Dharani Sri"];
  dash = ["../../assets/images/welcome.gif","../../assets/images/engage.gif", "../../assets/images/wed.gif" , "../../assets/images/reception.gif","../../assets/images/map.gif","../../assets/images/contact.gif"];
  imagelist = ["welcome","engagement","wedding","reception","map","contact"];
  // constructor(private route: ActivatedRoute) { 
  //   this.route.params.subscribe(res => console.log(res.id))
  // }
constructor(){}

  ngOnInit() {
  }

  displayme(index){
  	alert(index+" , "+this.imagelist[index]);
  }

}
